import globalConfig from '@/globalConfig'

const url = globalConfig.getWSHostName()
let socket = null;
const subscribed = new Map();

let isConnected = false;
let expectedToBeConnected = false;

function getPromise () {
    let resolve, reject;
    let promise = new Promise((res, rej) => {
        resolve = res;
        reject = rej;
    });
    return { promise, resolve, reject };
}

function connect () {
    if (socket !== null) return;
    socket = new WebSocket(url);

    socket.onopen = () => {
        socket.send(JSON.stringify({
            sessionId: localStorage.exchangeClientSession
        }))
    }
    socket.onmessage = (e) => { // recieving message from client
        try {
            let message = JSON.parse(e.data)

            if(subscribed.has(message.entityName)) {
                subscribed.get(message.entityName)(message.data);
            }

        } catch (e) {
            console.log(e);
        }
    };

    socket.onerror = (e) => {
        console.log("error", e);
        socket = null;
    };

    socket.onclose = (e) => {
        console.log("close", e);
        socket = null;
    };
}

function disconnect() {
    if(socket !== null) {
        socket.close();
        socket = null;
    }
}



function updateConnectionStatus () {
    if(expectedToBeConnected && !isConnected) {
        connect();
    } else {
        disconnect();
    }
}

setInterval(() => {
    updateConnectionStatus()
}, 1000);

const wsApi = {
    subscribe: async (entityName, cb) => {
        socket.send(JSON.stringify({
            subscribe: entityName
        }))
        subscribed.set(entityName, cb);
    },
    unsubscribe: async (entityName) => {
        socket.send(JSON.stringify({
            unsubscribe: entityName
        }))
        subscribed.delete(entityName);
    },

    connect: async () => {
        isConnected = true;
        for(let [entityName, cb] of subscribed) {
            wsApi.subscribe(entityName, cb);
        }
    },

    disconnect: async () => {
        isConnected = false;
        subscribed.clear();
    }

    
    // sendMessageForClient: async (text, ticketId) => {
    //     const data = {
    //         text,
    //         ticketId
    //     };
    //     socket.send(JSON.stringify(data));
    //     return null;
    // },
};

export { wsApi };