import { api } from '@/plugins/api'
import localizeFilter from '@/filters/localize.filter'
import { wsApi } from '../plugins/wsApi'

export default {
    state: () => ({
        walletData: null,
        walletCoins: []
    }),
    getters: {
        walletData: state => state.walletData,
        walletCoins: state => state.walletCoins,
        walletCoinByTicker: (state, getters) => (coinTicker) => {
          return getters.walletCoins.filter(coin => {
            return (coin.coinTicker === coinTicker)
          })[0]
        }
    },
    actions: {
        async getWalletData({ dispatch, commit }) {
            // const data = await api.getWalletData()
            const data = {
                coinTicker: 'CUTV',
                amount: 1000000,
                amountUSD: 45232399.65,
                rateChange: 0.04,
            }
            commit('SET_WALLET_DATA', data)
        },
        async getWalletCoins({ dispatch, commit }) {
            // const coins = await api.getWalletCoins()
            const coins = [
                {coinTicker: 'GDCV', coinName: 'GadgetCoin Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
                {coinTicker: 'CWPTV', coinName: 'CWPT Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
                {coinTicker: 'ITCV', coinName: 'InternetCoin Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
                {coinTicker: 'CUTV', coinName: 'CuprumToken Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
                // {coinTicker: 'BTCV', coinName: 'Bitcoin Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
                // {coinTicker: 'ETHV', coinName: 'Ethereum Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
                {coinTicker: 'USDV', coinName: 'USDT Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
              ]
            commit('SET_WALLET_COINS', coins)
        },
        async getWalletCoin({ dispatch, commit }, { coinTicker }) {
            // const coin = await api.getWalletCoin({ coinTicker })
            const coin = {coinTicker: coinTicker, coinName: '', amount: 0.00, amountUSD: 0, rateChange: -0.0665}
            commit('SET_WALLET_COIN', {coin})
        }
    },
    mutations: {
        SET_WALLET_DATA(state, data) {
            state.walletData = data
        },
        SET_WALLET_COINS(state, data) {
            state.walletCoins = data
        },
        SET_WALLET_COIN(state, {coin}) {
            let idx = -1
            for (let i=0; i<state.walletCoins.length; i++) {
                if (state.walletCoins[i].coinTicker === coin.coinTicker) {
                    idx = i
                }
            }
            if (idx !== -1) {
                state.walletCoins[idx] = JSON.parse(JSON.stringify(coin))
            } else {
                state.walletCoins.push(JSON.parse(JSON.stringify(coin)))
            }
        },
    }
}