        exchangeSelectedOrderStatuses: state => {
            return state.exchangeSelectedOrderStatuses
        },
        exchangeSelectedOrderTab:  state => {
          return state.exchangeSelectedOrderTab
        },
        statusesFromTab: (state, getters)  => (tab) => {
          if (tab === 'current') {
            return ['waiting', 'pending']
          } else {
            return [tab]
          }
        },
        allExchangeOrders: (state, getters)  => (tab) => {
            let result = []
            const statuses = getters.statusesFromTab(tab)
            statuses.forEach((status) => {
              const orders = state.exchangeOrders.filter(order => {
                return order.status === status
              })
              for (let o of orders) {
                result.push(o)
              }
            })
            return result
        },
        exchangeOrders: (state, getters) => (selectedTab, skip, limit) => {
          return getters.allExchangeOrders(selectedTab).slice(skip, skip+limit)
        },
        exchangeMajorCoinsPairs: (state, getters) => (coinTicker) => {
          return state.exchangeMajorCoinsPairs.filter(el=>{
            return el.coin === coinTicker
          })[0]
        },
        candlestickChartData: state => {
            return state.candlestickChartData
        },
