    mutations: {
        SET_COINS(state, data) {
            state.exchangeCoins = data
        },
        SET_COINS_PAIRS(state, data) {
            state.exchangeCoinsPairs = data
        },
        SET_SELECTED_COINS_PAIR(state, data) {
            const firstCoin = data.firstCoin
            const lastCoin = data.lastCoin
            var coinsPair = state.exchangeCoinsPairs.filter(pair => {
              return pair.firstCoin === firstCoin && pair.lastCoin === lastCoin
            })[0]
            state.exchangeSelectedCoinsPair = coinsPair
        },
        SET_SELECTED_EXCHANGE_ACTION(state, data) {
            state.exchangeSelectedAction = data
        },
        SET_SELECTED_EXCHANGE_ORDER_TYPE(state, data) {
            state.exchangeSelectedOrderType = data
        },
        SET_SELECTED_EXCHANGE_ORDER_TAB(state, data) {
            state.exchangeSelectedOrderTab = data
        },
        SET_CANDLESTICK_CHART_DATA(state, data) {
            state.candlestickChartData = data
        },
        CHANGE_CANDLESTICK_CHART_DATA(state, data) {
            const max = 20
            const min = 0
            const idx = Math.floor(Math.random() * (max - min + 1) + min)
            state.candlestickChartData[idx].y = data
        },
        CHANGE_EXCHANGE_GLASS_DATA(state) {
          const maxPrice = 1000.0
          const minPrice = 100.0
          const price = Math.floor(Math.random() * (maxPrice - minPrice + 1) + minPrice)
          const maxAmount = 500
          const minAmount = 100
          const amount = Math.floor(Math.random() * (maxAmount - minAmount + 1) + minAmount)
          const changeSellOrders = Math.random() < 0.5;
          if (changeSellOrders) {
            const max = state.exchangeGlassData.sellOrders.length-1
            const min = 0
            const idx = Math.floor(Math.random() * (max - min + 1) + min)
            state.exchangeGlassData.sellOrders[idx] = {price: price, amount: amount}
          } else {
            const max = state.exchangeGlassData.buyOrders.length-1
            const min = 0
            const idx = Math.floor(Math.random() * (max - min + 1) + min)
            state.exchangeGlassData.buyOrders[idx] = {price: price, amount: amount}
          }
      },
    },

