// test using mirage js
// if (process.env.NODE_ENV === "development") {
//     makeServer()
// }

// dom burning timer
const timestamp = 1654290000000
// const timestamp = +new Date() + 10000
store.dispatch('runDomBurnTimer', {timestamp: timestamp})

document.addEventListener('visibilitychange', function() {
  if(document.hidden) {
      // tab is now inactive
      // temporarily clear timer using clearInterval() / clearTimeout()
      store.dispatch('stopDomBurnTimer')
  }
  else {
      // tab is active again
      // restart timers
      store.dispatch('runDomBurnTimer', {timestamp: timestamp})
  }
});

Array.prototype.max = function() {
  return Math.max.apply(null, this);
};

Array.prototype.min = function() {
  return Math.min.apply(null, this);
};

const app = createApp({
  extends: App,
  created() {
    // do something when page is reloaded
    // ...
  },
})
app.use(store)
app.use(router)
app.use(messagePlugin)
app.use(Particles)
app.use(VueResizeText)
app.component('pagination', Pagination);
app.directive('maska', maska);
app.config.globalProperties.$filters = filters
app.config.globalProperties.$localizeFilter = localizeFilter
app.config.globalProperties.$dateTimeFilter = dateTimeFilter
app.config.globalProperties.$utils = utils
// add other global variables
// ...
app.config.globalProperties.$gb = {}
app.config.globalProperties.$gb.vuetimeline = {}
app.config.globalProperties.$gb.vuetimeline.theme = "dark" //(options || {}).theme || "dark"
store.$app = app;
app.use(VueGtag, {
    config: { id: "G-GX60JRLS2S" }
  }).mount('#app')